@import url(https://fonts.googleapis.com/css2?family=Metal+Mania&family=Open+Sans+Condensed:wght@300;700&family=Open+Sans:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  height: 100vh;
    
}
.inner-container {
  background: url(/paragoria/static/media/pg-brain.00170a9b.png) no-repeat 50% 20%;
  background-attachment: fixed;
  background-size: 80vw;
  min-height: 100vh;
  -webkit-animation: beat 1.3s infinite;
    animation: beat 1.3s infinite;
}
@-webkit-keyframes beat {
  0%   {background-size: 83vw;}
  5%  {background-size: 79vw;}
  10%  {background-size: 80vw;}
  100%  {background-size: 80vw;}
}
@keyframes beat {
  0%   {background-size: 83vw;}
  5%  {background-size: 79vw;}
  10%  {background-size: 80vw;}
  100%  {background-size: 80vw;}
}


.homepage-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background: url(/paragoria/static/media/background_s.5fb8cef3.png) no-repeat center;
  background-attachment: fixed;
  background-size: 100% 100%;
}

@media only screen and (min-width: 770px) {
  .homepage-container {
    background: url(/paragoria/static/media/background_m.ba5f6a94.png) no-repeat center top;
    background-attachment: fixed;
    background-size: 100% 100%;
  }
}

@media only screen and (min-width: 1200px) {
  .homepage-container {
    background: url(/paragoria/static/media/background_l.f961df69.png) no-repeat center top;
    background-attachment: fixed;
    background-size: 100% 100%;
  }
}

.container {
    height: 100%;
    background: linear-gradient(180deg, #000000 0%, #000000 40%, #632828 50%, #000000 60%, #000000 100%);
    background-size: 240% 240%;

    -webkit-animation: AnimationName 20s infinite;
    animation: AnimationName 20s infinite;
}

@-webkit-keyframes AnimationName {
    0%{background-position:30% 0%}
    50%{background-position:70% 100%}
    100%{background-position:30% 0%}
}
@keyframes AnimationName {
    0%{background-position:30% 0%}
    50%{background-position:70% 100%}
    100%{background-position:30% 0%}
}


/* HEADER */

header {
  max-width: 800px;
  margin: auto;
  padding: 0.3em;
  text-align: center;
}
header img {
  max-width: 650px;
  margin: -0.5em auto 0.3em auto;
  width: 100%;
}

/* NAV */

nav ul {
  margin-top: 0;
  display: flex;
  flex-wrap: wrap;
  background-color: rgba(200, 200, 200, 0.6);
  justify-content: center;
  padding: 0;
}

nav li {
  display: inline;
  padding: 0.8em 0;
  margin: 0 0.5em;   
}

nav a {
  font-family: 'Open Sans Condensed', sans-serif;
  font-size: 1.05em;
  font-weight: 700;
  text-decoration: none;
  padding: 0.8em;
  color: #222;
  text-transform: uppercase;
  border-radius: 5px;
  transition: 0.25s;
}

nav a:hover {
  position: relative;
  background-color: #555; 
  top: 5px;
  color: #afafaf;
}

nav li:hover {
  background-color: #555;  
}

nav li:active, nav a:active {
  background-color: #3b3b3b;  
}

.selected {
  color: #afafaf;
  background-color: #3b3b3b;
  border-radius: 0;
}

/* FOOTER */

footer {
  text-align: center;
  width: 100%;
  margin: auto; 
}

footer ul {
  list-style: none;
  margin-bottom: 0;
  padding-left: 0;
}

footer li {
  display: inline-block;
}

footer a {
  display: inline-block;
  color: #222;
  height: 1.5em;
  padding: 0.6em;
  width: 1.5em;
  margin: 1em;
  border-radius: 50%;
  background-color: rgba(200, 200, 200, 0.6);
  transition: 0.25s;
}

footer a:hover {
  cursor: pointer;
  background-color: #555; 
  border-radius: 0;
  color: #afafaf;
}
section {
    text-align: center;
    color: #afafaf;
}

.section-header {
    padding: 3em;
    margin: 1.5em 0;
    background-color: rgba(0, 0, 0, 0.4);
}

section h2 {
    font-family: 'Metal Mania', cursive;
    letter-spacing: 0.01em;
    font-size: 2.2em;
    margin: 0.1em;
}

.sub-text {
    font-size: 1.1em;
    color: #555;
}

form {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 1.5em auto;
    width: 90%;
    max-width: 600px;
}

input, textarea {
    background-color: #afafaf;
    color: #000;
    opacity: 0.6;
    border: 1px solid #555;
    font-size: 1em;
    padding: 0.3em;
    margin: 0.4em;
    font-family: Arial, Helvetica, sans-serif;
}
input:focus, textarea:focus {
    border: 1px solid #a52121;
    background-color: #a08f8f;
}
input:hover, textarea:hover {
    border: 1px solid #a52121;
    box-shadow: 2px 2px 8px #632828, -2px -2px 8px #632828;
}
input {
    flex: 1 1;
}
textarea {
    width: 100%;
    height: 6.8em;
}
form button {
    width: 7em;
    padding: 0.5em;
    font-size: 1em;
    color: #afafaf;
    background-color: #632828;
    border: none;
    border-radius: 3px;
    margin: 0.5em;
}

form button:hover {
    cursor: pointer;
    background-color: #a52121;
    box-shadow: 2px 2px 8px #632828, -2px -2px 8px #632828;
}
form button:active {
    position:relative;
    background-color: #882929;
    top: 1px;
    left: 1px;
}
.section-footer {
    line-height: 1.2em;
    color: #afafaf;
}
.section-footer h4 {
    font-family: 'Open Sans Condensed', sans-serif;
    font-weight: 700;
}
.section-footer p {
    font-family: 'Open Sans', sans-serif;
    line-height: 0.5em;
    font-size: 0.8em;
}

/* BIO */
.bio-disc-lu {
    max-width: 1200px;
    margin: auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.lineup-disc {
    flex: 1 1;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.lineup, .discography {
    width: 280px;
    margin: 1em;
    padding: 0 1em 0.5em 1em;
    border: 1px solid #632828;
    background-color: rgba(99, 40, 40, 0.3);
}

.lineup .header, .discography .header {
    font-family: 'Metal Mania', cursive;
    position: relative;
    top: -1em;
    font-size: 1.2em;
    margin-bottom: 1em;
}
.logo {
    position: relative;
    top: 1.7em;
    width: 175px;
}
.bio {
    font-family: 'Open Sans', sans-serif;
    font-size: 1.1em;
    max-width: 700px;
    text-align: justify;
    padding: 1em;
    margin: 1em;
}

.bio figure {
    margin: 2em 0.7em;
}

.bio img {
    width: 100%;
    margin: auto;
    text-align: center;
}
.bio figcaption {
    font-family: 'Open Sans Condensed', sans-serif;
    font-size: 0.9em;
    text-align: right;
}

.headpara {
    font-family: 'Open Sans', sans-serif;
    font-size: 1.1em;
}


.subpara {
    display: block;
    color: #555;
    padding-top: 0em;
    padding-bottom: 0.7em;
    font-family: 'Open Sans Condensed', sans-serif;
    font-weight: 700;
}

.discography figure img {
    width: 100%;
}

.img-belt {
    width: 80%;
    max-width: 1000px;
    margin: 2em auto;
}
.img-belt img {
    opacity: 0.7;
}
.video-belt {
    display: flex;
    justify-content: center;
}
.img-belt img:hover {
    opacity: 1;
    box-shadow: 1px 1px 7px #632828, -2px -2px 7px #632828;
}
.loading {
    margin: auto;
}

.news {
    max-width: 900px;
    margin: auto;
    padding: 2em;
    font-family: 'Open Sans', sans-serif;
    font-size: 1.1em;
}
.news .date {
    font-family: 'Open Sans Condensed', sans-serif;
    font-weight: 700;
    font-size: 1.2em;
    color: #555;
}

@media only screen and (max-width: 740px) {
    .bio figure {
        display: none;
    }
    .video-belt {
        display: none;
    }
}
